import React, { Component } from 'react';

import './App.css';
import Products from './Components/Products'

class App extends Component {

  state = {
    productsTitle: 'Ты сегодня покормил кота?',
    products: [{
      id: 0,
      name: 'Нямушка',
      type: 'с фуа-гра',
      ingredients: 'Печень утки разварная с артишоками.',
      quantity: '10',
      gift: 'мышь',
      additionalInf: '',
      weightInKg: '0,5',
      inStock: true
    },
    {
      id: 1,
      name: 'Нямушка',
      type: 'с рыбой',
      ingredients: 'Головы щучьи с чесноком да свежайшая сёмгушка.',
      quantity: '40',
      gift: '2 мыши',
      additionalInf: '',
      weightInKg: '2',
      inStock: true
    },
    {
      id: 2,
      name: 'Нямушка',
      type: 'с курой',
      ingredients: 'Филе из цыплят с трюфелями в бульоне.',
      quantity: '100',
      gift: '5 мышей',
      additionalInf: 'заказчик доволен',
      weightInKg: '5',
      inStock: false
    }],
    selectedProducts: []
  }
  onCardClick = (id) => {
    const selectedProducts = this.state.selectedProducts
    if (selectedProducts.includes(id)) {
      selectedProducts.splice(selectedProducts.indexOf(id), 1)
    } else {
      selectedProducts.push(id)
    }
    this.setState(() => (selectedProducts))
  }


  render() {
    const nav = (
      <>
        <a href="https://de4ev.github.io/projects/funbox/" className="nav-arrow">
          <div className="circle">
            <div className="arrow"></div>
          </div>
        </a>
        <a href="https://github.com/de4ev/funbox_test/react-cat-food" className="nav-link">Исходники</a>
      </>
    )
    return (
      <div className="App">
        {nav}
        <div className="container">
          <Products
            products={this.state.products}
            productsTitle={this.state.productsTitle}
            selectedProducts={this.state.selectedProducts}
            onCardClick={this.onCardClick}
          />
        </div>
      </div>
    );
  }
}

export default App;
