import React, { Component } from 'react';

import './ItemCard.scss'
import catImg from '../assets/cat-image.png'

class ItemCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hoverSelected: false,
      leavedSelected: false
    };
  }

  onCardClick = () => {
    if (this.props.product.inStock) {
      this.props.onCardClick(this.props.product.id)
    }
    if (this.props.isSelected) {
      this.setState(() => ({
        hoverSelected: false,
        leavedSelected: false
      })
      )
    }
  }
  onMouseOver = () => {
    if (this.state.leavedSelected && this.props.product.inStock) {
      this.setState(() => ({
          hoverSelected: true
        })
      )
    }
  }
  onMouseOut = () => {
    this.setState(() => ({
        hoverSelected: false,
        leavedSelected: this.props.isSelected
      })
    )
  }

  render() {
    const cardClasses = ['card']
    if (!this.props.product.inStock) {
      cardClasses.push('disabled')
    } else if (this.props.isSelected) {
      cardClasses.push('selected')
      if (this.state.hoverSelected) {
        cardClasses.push('hover')
      }
    }

    return (
      <div className="card-wrapper">
        <div
          onMouseEnter={this.onMouseOver}
          onMouseLeave={this.onMouseOut}
          onClick={this.onCardClick}
          className={cardClasses.join(' ')}
        >
          <div className="card-body">
            {this.state.hoverSelected
              ? <p className="product-topline text-rose">Котэ не одобряет?</p>
              : <p className="product-topline">Сказочное заморское яство</p>
            }
            <h3 className="product-title">{this.props.product.name}<span>{this.props.product.type}</span></h3>
            <p><span className="text-bold">{this.props.product.quantity}</span> порций</p>
            {this.props.product.gift
              && <p><span className="text-bold">{parseInt(this.props.product.gift) || ''}</span>
                {this.props.product.gift.slice(this.props.product.gift.indexOf(parseInt(this.props.product.gift)) + 1)}
                <span> в подарок</span></p>}
            {this.props.product.additionalInf
              && <p>{this.props.product.additionalInf}</p>}
            <img
              className="product-image"
              src={catImg}
              alt="Голодный кот, на шее кулон Funbox"
            />
            <div className="product-weight">{this.props.product.weightInKg}<span>кг</span></div>
          </div>
        </div>
        {!this.props.product.inStock
          ? <p className="card-text text-yellow">Печалька, {this.props.product.type} закончился.</p>
          : this.props.isSelected
            ? <p className="card-text">{this.props.product.ingredients}</p>
            : <p className="card-text">Чего сидишь? Порадуй котэ, <span className="buy-text-button" onClick={this.props.onCardClick.bind(this, this.props.product.id)}> купи</span><span className="text-blue">.</span>
            </p>
        }
      </div>
    )
  }
}

export default ItemCard